import React from 'react';
import Technologies from './components/Technologies/Technologies';
import './Project.scss';

const Project = ({project}) => {
  const description = project.description.map(paragraph => <div>{paragraph}</div>);
  return (
    <div className="project">
      <h2 className="title">{project.title}</h2>                   
        <div className="section">                                 
          <div className="description">{description}</div>
        </div>
        <div className="section">
          <div className="sub-title">Technology Stack:</div>                    
            <Technologies technologies={project.stack} />
        </div>
        <div className="section source-code">
          <div className="sub-title">Source Code:</div>             
          <a className="source-code-link" href={project.sourceCode}>{project.sourceCode}</a>  
        </div>
    </div>
  );
}

export default Project;