import React from 'react';
import Page from "../../components/Page/Page";
import SlantedSection from  "./components/SlantedSection/SlantedSection";
import Project from './components/Project/Project';
import queryString from 'query-string';
import { COLOR } from '../../services/colors';

const project = {
    title: "Job Site Manager UI",
    description: ["The Job Site Manager App is a web application that helps companies who operate at multiple different job sites manage and track which employees are assigned to each site. The Job Site Manager UI is the front end for the application. It supports a range of functionality including adding new employees, assigning employees designated roles, creating jobs sites and adding/removing employees with the necessary qualifications to a job. ",
    "While building this application I was able to leverage some new technologies in the React development stack. I used React’s new feature hooks, giving me the ability to keep reduce the complexity of my components by using only simple functional components. I found hooks to be very intuitive and got me even more excited about the future of React. I also got my first taste of css-in-js by styling my components using the Styled-Components library. I’m still on the fence about Styled-Components but I definitely see the benefit of having all related functionality in one file."],
    stack: [
      {
        name: "React",
        link: "https://reactjs.org/"
      },
      {
        name: "Redux",
        link: "https://redux.js.org/"
      },
      {
        name: "Styled Components",
        link: "https://www.styled-components.com/"
      },
      {
        name: "Jest",
        link: "https://jestjs.io/"
      },
      {
        name: "Flexbox",
        link: "https://css-tricks.com/snippets/css/a-guide-to-flexbox/"
      }
    ],
    sourceCode: "https://gitlab.com/Job-Site-Manager/job-site-manager-ui"
  }

  const ProjectComponent = <Project project={project} />;

const Projects = ({location}) => {
    const section = location && location.search && queryString.parse(location.search).section;

    const sectionNames = ["Front End", "Back End", "DevOps", "Mobile"];



    const content = (
        <div className="container">
            <SlantedSection header={{ color: COLOR.green, title: "Front End" }} items={[ProjectComponent, ProjectComponent]} />
            <SlantedSection header={{ color: COLOR.orange, title: "Back End" }} items={[ProjectComponent, ProjectComponent, ProjectComponent]} />
            <SlantedSection header={{ color: COLOR.blue, title: "DevOps" }} items={[ProjectComponent]} />
            <SlantedSection header={{ color: COLOR.yellow, title: "Mobile" }} items={[ProjectComponent]} />
        </div>
    );

    return (
        <div>
            <Page 
                content={content} 
                startingLocation={section} 
                sectionNames={sectionNames} 
            />
        </div>
    );
};

export default Projects;